import Navbar from '../Navbar';
import DashboardNavbar from '../DashboardNavbar'
import React, {useState, useEffect} from 'react';
import {Button, Stack, Grid, TextField, Input, MenuItem, Select, InputLabel, FormControl, ButtonGroup} from '@mui/material';
import useFetch3 from "../../Requester/useFetch3";
import AvatarPlate from '../AvatarPlate';
import AvatarPlateSmall from '../AvatarPlateSmall';



const Dashboard = () => {
document.body.style = 'background: #E0E0E0;';
const [name, setName] = useState("Jméno a Příjmení");
const [imageName, setImageName] = useState("5");
const [description, setDescription] = useState("Hrubý popis učitele, co dělal, jaký je a za co se zasloužil, proč je to zrovna nejlepší učitel, kterého kyd univerzita viděla a co dbude dělat potom.");
const [message, setMessage] = useState("");
const [faculty, setFaculty] = useState(['PrF', 'Přírodovědecká fakulta']);
const {data3, setData3} = useFetch3();
const [file, setFile] = useState(null);
const [imageURL, setImageURL] = useState(null);
console.log(data3);
useEffect(() => {
    let m = `http://pedagogium.onrender.com/teachers/api/faculty/${faculty[0]}`;
          setData3({ ...data3, slug: m });    
setTimeout(()=>{
    }, 100)
  
  }, [faculty])



let imageHandling = (e) => {
  setFile(e.target.files[0])
  setImageURL(URL.createObjectURL(e.target.files[0]));
  }

let handleSubmit = async (e,x) => {

  e.preventDefault();
  const formData = new FormData();
  formData.append('image', file);
  console.log(formData);
  console.log(file.name)

  fetch('https://pedagogium.onrender.com/teachers/api/image', {
    method: 'POST',
    body: formData
  })
  .then(response => response.json())
  .then(data => {
    fetch('https://pedagogium.onrender.com/teachers/api/teacher', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            name: name,
            caption: description,
            imageURL: file.name, //image name
            faculty: faculty[0]
    })})
  })
  .catch(error => {
    console.error(error);
  });
  setTimeout(()=>{
    window.location.reload();
  }, 250)

    }

return(
<div style={{position: 'absolute', top: 0, width: '100%'}}>
<Navbar/>
<div id="MainDashboard"><br></br>
<DashboardNavbar/>
    <h1>Excellentní učitelé</h1><hr></hr>
    <FormControl fullWidth>
  <InputLabel id="demo-simple-select-label">Fakulta</InputLabel>
  <Select

    label="faculty"
    onChange={(e) => setFaculty(e.target.value)}
   >
    <MenuItem value={['PrF','Přírodovědecká fakulta']}>Přírodovědecká fakulta</MenuItem>
    <MenuItem value={['LF1', '1. Lékařská fakulta']}>1. Lékařská fakulta</MenuItem>
    <MenuItem value={['LF2', '2. Lékařská fakulta']}>2. Lékařská fakulta</MenuItem>
    <MenuItem value={['LF3', '3. Lékařská fakulta']}>3. Lékařská fakulta</MenuItem>
    <MenuItem value={['LFPLZ', 'Lékařská fakulta v Plzni']}>Lékařská fakulta v Plzni</MenuItem>
    <MenuItem value={['LFHRDC', 'Lékařská fakulta v Hradci Králové']}>Lékařská fakulta v Hradci Králové</MenuItem>
    <MenuItem value={['FaF', 'Farmaceutická fakulta']}>Farmaceutická fakulta</MenuItem>
    <MenuItem value={['FSV', 'Fakulta Sociálních věd']}>Fakulta sociálních věd</MenuItem>
    <MenuItem value={['FHS', 'Fakulta humanitních studií']}>Fakulta humanitních studií</MenuItem>
    <MenuItem value={['FF', 'Filozofická fakulta']}>Filozofická fakulta</MenuItem>
    <MenuItem value={['MFF', 'Matematicko-fyzikální fakulta']}>Matematicko-fyzikální fakulta</MenuItem>
    <MenuItem value={['KTF', 'Katolická teologická fakulta']}>Katolická teologická fakulta</MenuItem>
    <MenuItem value={['ETF', 'Evangelická teologická fakulta']}>Evangelická teologická fakulta</MenuItem>
    <MenuItem value={['HTF', 'Husitská teologická fakulta']}>Husitská teologická fakulta</MenuItem>
    <MenuItem value={['PF', 'Právnická fakulta']}>Právnická fakulta</MenuItem>
    <MenuItem value={['PEF','Pedagogická fakulta']}>Pedagogická fakulta</MenuItem>
    <MenuItem value={['FTVS', 'Fakulta tělesné výchovy a sportu']}>Fakulta tělesné výchovy a sportu</MenuItem>
  </Select>
</FormControl>
<Stack direction="row" spacing={2} sx={{width: '100%'}}>
    <div style={{width: "350px", height: '600px'}}>
<AvatarPlate name={name} description={description} fileName={imageName} fileURL={imageURL} faculty={faculty[1]}/>
    </div>
    <div><br></br>
        <TextField id="standard-basic" 
    label="Name" 
    variant="standard"
    onChange={(e) => setName(e.target.value)}
    sx={{marginLeft: '12.5%', marginTop:'20%', marginRight: '25%', width: '350px', marginBottom:'20px'}}
    /><br></br>
        <TextField id="standard-basic" 
    label="Description" 
    variant="standard"
    multiline
    onChange={(e) => setDescription(e.target.value)}
    sx={{marginLeft: '12.5%', marginRight: '25%', width: '350px', marginBottom:'20px', overflow:'hidden'}}
    />
  <br></br>
    <form onSubmit={handleSubmit} style={{marginLeft:'15%', marginBottom:'5%', marginTop:'2%'}}>
      <input type="file" onChange={imageHandling} />
      <Button>Delete</Button>
      <Button type="submit" >SUBMIT</Button>
    </form>
    <ButtonGroup  sx={{marginLeft: '38.5%'}}>


    </ButtonGroup>
    </div>
</Stack><br></br>
<Stack direction="row" sx={{maxWidth:'150px', marginTop: '50px'}} spacing={2}>
{!data3.results ? "Loading..." : 
        data3.results.slice(0).reverse().map(({ _id, caption, data, faculty, imageURL, name}) =>
        <AvatarPlateSmall name={name} description={caption} fileName={imageName} imageURL={imageURL}/>
    )}

</Stack>

    
</div>
</div>);
}
export default Dashboard;