import Navbar from '../Navbar';
import DashboardNavbar from '../DashboardNavbar'
import React, {useState, useEffect} from 'react';
import {Button, Stack, Grid, TextField} from '@mui/material';
import { width } from '@mui/system';
import e from 'cors';



const Dashboard = () => {
  document.body.style = 'background: #E0E0E0;';
const [caption, setCaption] = useState("");
const [contentShort, setContentShort] = useState("");
const [content, setContent] = useState("");
const [imageURL, setImageURL] = useState("");
const [message, setMessage] = useState("");
const [url, setUrl] = useState("");
const [section, setSection] = useState("");
const [file, setFile] = useState(null);

let handleSubmit = async (e) => {
try{  
        //uloží post do databáze
        let res = await fetch("https://pedagogium.onrender.com/posts/api/post", {
          method: "POST",
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            caption: caption,
            contentShort: contentShort, 
            content: content,
            url: url,
            imageURL: file.name,
            section: "science",
          }),
        });
        let resJson = await res.json();
        if (res.status === 201) {
          setCaption("");
          setContentShort("");
          setContent("");
          setImageURL("");
          setUrl("");
          setSection("")
          setMessage("Příspěvek byl úspěšně zaevidován");
          const formData = new FormData();
          formData.append('image', file);
          fetch('https://pedagogium.onrender.com/posts/api/image', {
              method: 'POST',
              body: formData
            })
            .then(response => response.json())
            .then(data => {
            })
            .catch(error => {
              console.error(error);
            });
            setTimeout(()=>{
              window.location.reload();
            }, 250)
            alert("Příspěvek úspěšně zaevidován")
                } else {
                    setMessage("Request has failed");
                  }
        } catch (err) {
                  console.log(err);
                }}

return(<div style={{position: 'absolute', top: 0, width: '100%'}}>
<Navbar/>
<div id="MainDashboard"><br></br>
<DashboardNavbar/>
    <h1>Přidat článek</h1><hr></hr>
    <TextField id="standard-basic" 
    label="Název článku" 
    variant="standard"
    onChange={(e) => setCaption(e.target.value)}
    sx={{width: '50%', marginLeft: '12.5%', marginRight: '25%'}}
    /><br></br>
    <TextField id="standard-basic" 
    label="Abstrakt nebo popis článku" 
    variant="standard" 
    onChange={(e) => setContentShort(e.target.value)}
    sx={{width: '75%', marginLeft: '12.5%', marginRight: '12.5%', marginTop:'25px'}}/><br></br>
    <TextField id="standard-basic" 
    label="Text" 
    multiline variant="standard" 
    onChange={(e) => setContent(e.target.value)}
    sx={{width: '75%', marginLeft: '12.5%', marginRight: '12.5%', marginTop:'25px'}}/><br></br>
    <TextField id="standard-basic" 
    label="Odkzaové url (volitelné)" 
    variant="standard"
    onChange={(e) => setUrl(e.target.value)}
    sx={{width: '50%', marginLeft: '12.5%', marginRight: '25%'}}
    /><br></br>
    
    <input type="file" onChange={(e) => setFile(e.target.files[0])}></input>
    <br></br><br></br>
    <Button onClick={(e) => handleSubmit(e,'science')} color="error" sx={{marginLeft: '40%', marginRight: '40%', marginTop:'25px', marginBottom:'25px'}}>Vytvořit</Button>
</div>
</div>);





}
export default Dashboard;