import React, {useState, useEffect} from 'react';
import Navbar from './Navbar'
import teacherImg from '../Media/course.jpg';
import CoursesNavbar from './CoursesNavbar'
import {CardContent, Card, Avatar, Typography, Stack, Paper} from '@mui/material';

const Courses = () => {
return(<div>
<Navbar/><br></br>
<CoursesNavbar></CoursesNavbar><br></br><br></br>
<Paper sx={{width: '65%', display: 'block', margin: 'auto', marginTop: '50px'}}>
<div style={{textAlign: 'justify', padding: '25px'}}>
<h1>Obsah kurzu pedagogických dovedností</h1><img src={teacherImg} style={{maxWidth: '500px', float: 'right'}}></img>
<p>Tento kurz pro pedagogy na vysoké škole zaměřený na zlepšování jejich schopností učení nabízí příležitost pro rozvoj dovedností a schopností v oblasti výuky. Kurz se zaměřuje na moderní výukové strategie a technologie, rozvoj kompetencí studentů, hodnocení a zpětnou vazbu a osobní rozvoj pedagogů. Cílem kurzu je podpořit efektivnější učení a rozvoj studentů a vytvořit prostředí pro inovativní a kreativní výuku na vysoké škole.</p>
<div>
  <h3>Obsah kurzu:</h3>
  <ul>
    <li>Moderní výukové strategie a technologie</li>
    <li>Rozvoj kompetencí studentů (kritické myšlení, problémové řešení, komunikace, spolupráce)</li>
    <li>Hodnocení a zpětná vazba</li>
    <li>Rozvoj osobních dovedností a schopností pedagoga</li>
  </ul>

  <h3>Cíle kurzu:</h3>
  <ul>
    <li>Pomoci pedagogům rozvíjet své dovednosti a schopnosti v oblasti výuky.</li>
    <li>Podpořit efektivnější učení a rozvoj studentů.</li>
    <li>Vytvořit prostředí pro inovativní a kreativní výuku na vysoké škole.</li>
  </ul>

  <h3>Zaměření kurzu:</h3>
  <ul>
    <li>Zlepšení kvality výuky na vysoké škole.</li>
    <li>Rozvoj kompetencí pedagogů v oblasti moderních výukových strategií a technologií.</li>
    <li>Podpora pedagogů v rozvoji osobních dovedností a schopností.</li>
  </ul>
</div>
<div>
  <h3>Výhody absolventů tohoto kurzu:</h3>
  <ul>
    <li>Zlepšené dovednosti a schopnosti v oblasti výuky na vysoké škole, což jim umožní efektivněji vést výuku a pomoci studentům v jejich rozvoji.</li>
    <li>Zlepšené kompetence ve využívání moderních výukových strategií a technologií, což pomůže vytvořit inovativní a kreativní prostředí pro výuku na vysoké škole.</li>
    <li>Zlepšené schopnosti hodnocení a poskytování zpětné vazby, což zlepší kvalitu výuky a studenti tak budou mít větší příležitosti k rozvoji.</li>
    <li>Zlepšení osobních dovedností a schopností, což přispěje k celkovému rozvoji osobnosti a profesnímu růstu.</li>
  </ul>

  <p>Navíc budou absolventi kurzu odměněni<strong> extra finančním ohodnocením</strong> v podobě bonusu ve výši 10% z průměrného platu pedagoga na vysoké škole v dané zemi. Toto ohodnocení je určeno jako podpora a motivace pro další rozvoj a zlepšení výuky na vysokých školách.</p>
</div>
</div>

</Paper>
</div>
);
}
export default Courses;