import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import {Card, Color} from '@mui/material';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import {Button, ButtonGroup} from '@mui/material';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import Welcome from './Welcome'
import { ThemeProvider, createTheme } from '@mui/material/styles';
import { Link } from "react-router-dom";
import logo from '../Media/logo_violet.png';

export default function Navbar() {
  const theme = createTheme({
    palette: {
      primary: {
        main: "#f2f2f2"
      }
    }
  });
  return (
    <Card sx={{width: '45%', marginLeft:'28%', borderRadius: '10px', marginTop:'15px', marginBottom: "15px", zIndex: 5}} >
      <AppBar position="static">
        <Toolbar sx={{marginLeft: 'auto', marginRight: 'auto', textAlign: 'center'}}>
        <ButtonGroup   variant="text" aria-label="text button group">
          <Button href="/dashboard/event" variant="h6" >
            Akce
          </Button>
          <Button  href="/dashboard/excellent-teachers" variant="h6">
            Excelentní učitelé
          </Button>
          <Button  href="/dashboard/science" variant="h6">
            Věda
          </Button>
          <Button  href="/dashboard/inspiration" variant="h6">
            Inspirace
          </Button></ButtonGroup>
</Toolbar>
      </AppBar>
    </Card>
    
  );
}



