import Navbar from './Navbar'
import React, { useState, useEffect } from 'react';
import {Box, Tab, Tabs, Stack, Pagination, Typography} from '@mui/material';
import useFetch3 from "../Requester/useFetch3";
import Post from './Post'


const Inspiration = (props) => {
  const {data3, setData3} = useFetch3();
  const [value, setValue] = useState('beforecourse');
  const [page, setPage] = useState(1);

  console.log(data3.results);
  const handleChange = (event, valueR) => {
    setPage(valueR);
  };

  useEffect(() => {
      let m = "https://pedagogium.onrender.com/posts/api";
            setData3({ ...data3, slug: m });    
  setTimeout(()=>{
      }, 100)
    
    }, [])


return(<div><Navbar/><br></br>
<Stack direction='row' sx={{ marginTop: '100px', paddingBottom: '35px'}}>
  <div style={{width: '15%'}}>
  <Box sx={{marginTop: '50px', marginLeft: '10px'}}>
      <Tabs
        orientation="vertical"
        aria-label="Vertical tabs example"
        sx={{ borderRight: 1, borderColor: 'divider' }}>
        <Tab label="Před začátkem kurzu"  onClick={(e) => (setValue('beforecourse'), setPage(1))} />
        <Tab label="Před začátkem hodiny" onClick={(e) => (setValue('beforeclass'), setPage(1))}/>
        <Tab label="Během hodiny"  onClick={(e) => (setValue('duringclass'), setPage(1))} />
        <Tab label="Po skončení hodiny" onClick={(e) => (setValue('afterclass'), setPage(1))}/>
        <Tab label="Po skončení semestru"  onClick={(e) => (setValue('aftersemester'), setPage(1))}/>
        <Tab label="Další" onClick={(e) => (setValue('else'), setPage(1))}/>
      </Tabs>
    </Box>
  </div>
  <div style={{width: '70%', paddingLeft: '40px'}}> 
  {!data3.results ? "Loading..." : 
              data3.results.reverse().filter(function(d){
                return d.section.includes(value)}).slice((page-1)*10,(page-1)*10+10).map(({ _id, caption, contentShort, content, url, section, date, imageURL}) =>
          <Post caption={caption} contentShort={contentShort} content={content} imageURL={imageURL}  url={url} key={_id} _id={_id}></Post>
            )}</div>
  <div style={{width: '10%'}}></div>
</Stack>

<div style={{display: 'block', width: 'fit-content', margin: 'auto'}}>    <Pagination count={Math.ceil((data3.results.filter(function(d){
  return d.section.includes(value)}).length)/10)} page={page} onChange={handleChange} sx={{paddingBottom: '30px'}}/></div>
</div>);
}
export default Inspiration;