import Navbar from './Navbar';
import DashboardNavbar from './DashboardNavbar'
import React, {useState, useEffect} from 'react';
import DashboardStyles from '../Styles/DashboardStyles.css';
import {Button, Stack, Grid, TextField} from '@mui/material';
import { width } from '@mui/system';
import e from 'cors';



const Dashboard = () => {
  document.body.style = 'background: #E0E0E0;';
const [caption, setCaption] = useState("");
const [contentShort, setContentShort] = useState("");
const [content, setContent] = useState("");
const [imageURL, setImageURL] = useState("");
const [message, setMessage] = useState("");
const [section, setSection] = useState("");
localStorage.setItem('password', 'drsq89chem');
//let logo = require();

/*console.log(message);
console.log(caption);
console.log(contentShort);
console.log(content);
console.log(imageURL);
console.log(section);*/

let handleSubmit = async (e,x) => {
    console.log("jede");
try{  
        //uloží post do databáze
        let res = await fetch("https://pedagogium.onrender.com/posts/api/post", {
          method: "POST",
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            caption: caption,
            contentShort: contentShort, 
            content: content,
            imageURL: imageURL,
            section: x,
          }),
        });
        let resJson = await res.json();
        if (res.status === 201) {
          setCaption("");
          setContentShort("");
          setContent("");
          setImageURL("");
          setSection("")
          setMessage("Příspěvek byl úspěšně zaevidován");
         /* setTimeout(()=>{
          history("/home"); 
        }, 1000)*/
      } else {
          setMessage("Vysralo se to!");
        }
      } catch (err) {
        console.log(err);
      }}

//authentication
      const [authenticated, setAuthenticated] = useState(false);
      const [password, setPassword] = useState('');
    
      const handlePasswordChange = event => {
        setPassword(event.target.value);
      };
    
      const handleFormSubmit = event => {
        event.preventDefault();
        const savedPassword = localStorage.getItem('password');
        if (password === savedPassword) {
          setAuthenticated(true);
        }
      };
    
      if (!authenticated) {
        return (
          <form onSubmit={handleFormSubmit}>
            <label>
              Heslo:
              <input type="password" value={password} onChange={handlePasswordChange} />
            </label>
            <button type="submit">Submit</button>
          </form>
        );
      }
return(<div style={{position: 'absolute', top: 0, width: '100%'}}>
<Navbar/>
<div id="MainDashboard"><br></br>
<DashboardNavbar/>
    <h1>Přidat akci</h1><hr></hr>
  
    <TextField id="standard-basic" 
    label="Titulek" 
    variant="standard"
    onChange={(e) => setCaption(e.target.value)}
    sx={{width: '50%', marginLeft: '12.5%', marginRight: '25%'}}
    /><br></br>
    <TextField id="standard-basic" 
    label="Krátký popis" 
    variant="standard" 
    onChange={(e) => setContentShort(e.target.value)}
    sx={{width: '75%', marginLeft: '12.5%', marginRight: '12.5%', marginTop:'25px'}}/><br></br>
    <TextField id="standard-basic" 
    label="Text" 
    multiline variant="standard" 
    onChange={(e) => setContent(e.target.value)}
    sx={{width: '75%', marginLeft: '12.5%', marginRight: '12.5%', marginTop:'25px'}}/><br></br>
    <TextField id="standard-basic" 
    label="URL Image" variant="standard" 
    onChange={(e) => setImageURL(e.target.value)}
    sx={{width: '75%', marginLeft: '12.5%', marginRight: '12.5%', marginTop:'25px'}}/><br></br><br></br>
    <Button  onClick={(e) => handleSubmit(e,'events')}
    sx={{marginLeft: '40%', marginRight: '40%', marginTop:'25px'}} color="error">Vytvořit</Button>

    <h1>Přidat článek</h1><hr></hr>
    <TextField id="standard-basic" 
    label="Název článku" 
    variant="standard"
    onChange={(e) => setCaption(e.target.value)}
    sx={{width: '50%', marginLeft: '12.5%', marginRight: '25%'}}
    /><br></br>
    <TextField id="standard-basic" 
    label="Abstrakt nebo popis článku" 
    variant="standard" 
    onChange={(e) => setContentShort(e.target.value)}
    sx={{width: '75%', marginLeft: '12.5%', marginRight: '12.5%', marginTop:'25px'}}/><br></br>
    <TextField id="standard-basic" 
    label="Link ke článku" 
    multiline variant="standard" 
    onChange={(e) => setContent(e.target.value)}
    sx={{width: '75%', marginLeft: '12.5%', marginRight: '12.5%', marginTop:'25px'}}/><br></br>
    <TextField id="standard-basic" 
    label="URL Image" variant="standard" 
    onChange={(e) => setImageURL(e.target.value)}
    sx={{width: '75%', marginLeft: '12.5%', marginRight: '12.5%', marginTop:'25px'}}/><br></br><br></br>
    <Button onClick={(e) => handleSubmit(e,'science')} color="error" sx={{marginLeft: '40%', marginRight: '40%', marginTop:'25px', marginBottom:'25px'}}>Vytvořit</Button>
</div>
</div>);





}
export default Dashboard;