import React, {useState, useEffect} from 'react';
import {CardContent, Card, Avatar, Typography, Stack} from '@mui/material';
import useImage from "../Requester/useImage";


let Post = ({caption, contentShort, content, imageURL, _id, section, url, className,...rest }) => {
    const { loading, error, image } = useImage('Science/'+imageURL);
    if (error) return <Typography>{"error"}</Typography>

    const detail = (x, id) => {
        if(x!=""){
            let link = x;
            window.open(link)
        }else{
            let link = 'https://pedagogium.onrender.com/detail/id/'+id;
            window.open(link, "_blank")
        }
    }

   return(<div>
       <Card style={{marginTop: '10px'}} onClick={() => detail(url, _id)}>
        <CardContent>
            <Stack direction="row" spacing={2} sx={{width: '100%'}}>
                <div>{loading ? (
               <Typography>loading</Typography>
           ) : (
               <img
                   className={`Image${
                       className
                           ? className.padStart(className.length + 1)
                           : ''
                   }`}
                   src={image}
                   alt={'něco se pokazilo'}
                   style={{ maxWidth: '180px', marginLeft: '-3%',  borderRadius: '25px' }}
                   {...rest}
               />
           )}</div>
                <div>
        <Stack direction="column" spacing={2} sx={{width: '100%'}}>
        <h3 style={{fontSize: '14pt', marginBottom: '-15px', marginTop: '-1px'}}>{caption}</h3>
        <p style={{fontSize: '12pt', textAlign:'justify'}}>{contentShort}</p>
        </Stack></div>
            </Stack>
        </CardContent>
      </Card></div>
      );
}

export default Post;