import Navbar from './Navbar'
import React, { useEffect, useState} from 'react';
import {useParams} from "react-router";
import useFetch3 from "../Requester/useFetch3";
import { useNavigate } from "react-router-dom";
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import {Button, Stack} from '@mui/material';
import Typography from '@mui/material/Typography';
import useImage from "../Requester/useImage";


const DetailEvent = () => {
const [imageURL, setImageURL] = useState("");
const [className, setClassName] = useState("");
const { loading, error, image } = useImage('Science/'+imageURL);
document.body.style = 'background: #E0E0E0;';
const [postId, setPostId] = useState(useParams().id);
const {data3, setData3} = useFetch3();  

console.log(data3);
console.log(imageURL);

useEffect(() => {
    let m = "https://pedagogium.onrender.com/posts/api/id/"+postId;
          setData3({ ...data3, slug: m });    

 
  }, [])
  setTimeout(()=>{
    setImageURL(data3.results.imageURL)
      }, 500)
return(
  
<div style={{position: 'absolute', top: 0}}>
<Navbar/>
    <Card sx={{ minWidth: 275, marginLeft: '20%', marginRight: '20%', marginTop: '8%', float: 'left', textAlign: 'justify', display: 'flex'}} >
      <CardContent>
      <Typography sx={{ fontSize: 20, fontWeight: 'bold' }} color="text.primary" gutterBottom>
          {data3.results.caption}
        </Typography>
        <div id="stackLeft">
        <div>{loading ? (
               <Typography>loading</Typography>
           ) : (
               <img
                   className={`Image${
                       className
                           ? className.padStart(className.length + 1)
                           : ''
                   }`}
                   src={image}
                   alt={'něco se pokazilo'}
                   style={{ maxWidth: '250px', borderRadius: '25px' }}
                  
               />
           )}</div>
          

        <div id="stackRight">
        <Typography variant="body2">
          {data3.results.content}
          <br />
          {data3.results.date}
        </Typography>
        </div></div>
      </CardContent>
    </Card>
</div>);
}
export default DetailEvent;