import React, {useState, useEffect} from 'react';
import Navbar from '../Navbar'
import CoursesNavbar from '../CoursesNavbar'
import {CardContent, Card, Avatar, Typography, Stack, Paper} from '@mui/material';
import {TextField, FormGroup, Switch, InputLabel, Select} from '@mui/material';
import {MenuItem, Button, Snackbar, IconButton} from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';



const Register = () => {
    const [name, setName] = useState("");
    const [surname, setSurname] = useState("");
    const [email, setEmail] = useState("");
    const [telNumber, setTelNumber] = useState("");
    const [membership, setMembership] = useState("člen");
    const [publicationAuth, setPublicationAuth] = useState(true);
    const [yearValue, setYearValue] = useState(null);
    const [section, setSection] = useState("");
    const [startOfStudies, setStartOfStudies] = useState("");
    const [open, setOpen] = React.useState(false);
    const [message, setMessage] = useState("");
    
      const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
          return;
        }
    
        setOpen(false);
      };

      const action = (
        <React.Fragment>
          <Button color="secondary" size="small" onClick={handleClose}>
            CLOSE
          </Button>
          <IconButton
            size="small"
            aria-label="close"
            color="inherit"
            onClick={handleClose}
          >
            <CloseIcon fontSize="small" />
          </IconButton>
        </React.Fragment>
      );

    let handleSubmit = async (e) => {
        try{  
                //uloží člena do databaze
                let res = await fetch("", {
                  method: "POST",
                  headers: { 'Content-Type': 'application/json' },
                  body: JSON.stringify({
                    name: name,
                    surname: surname,
                    email: email,
                    telNumber: telNumber,
                    membership: membership,
                    publicationAuth: publicationAuth,
                    startOfStudies: startOfStudies,
                    section: section

                  }),
                });
                let resJson = await res.json();
                if (res.status === 201) {
                  console.log("Člen byl úspěšně zaevidován");
                  setMessage("Člen byl úspěšně zaevidován");
                  setOpen(true);
                  setTimeout(()=>{
                    window.location.reload();
                  }, 750)
                }
              } catch (err) {
                console.log(err);
                setMessage("Stala se nějaká chyba. (zatim nemáš hotovej backend)");
                setOpen(true);
              }}
    return (<div>
<Navbar></Navbar><br></br>
    <CoursesNavbar></CoursesNavbar>
<Snackbar
        open={open}
        autoHideDuration={6000}
        onClose={handleClose}
        message= {message}
        action={action}
      />
<Paper sx={{width:'70%', marginLeft:'auto', marginRight:'auto', marginTop: '85px', opacity: 0.9, '@media(max-width: 480px)' : {
   width: "95%", marginTop: "60px"
  }}}>
<FormGroup>
  <TextField id="standard-basic" 
        label="Jméno" 
        variant="standard" 
        onChange={(e) => setName(e.target.value)}
        sx={{width: '50%', marginLeft: '25%', marginTop:'25px', '@media(max-width: 480px)' : {width: "80%", marginLeft: "10%"}}}/><br></br>
    <TextField id="standard-basic" 
        label="Příjmení" 
        variant="standard" 
        onChange={(e) => setSurname(e.target.value)}
        sx={{width: '50%', marginLeft:'25%', marginTop:'25px', '@media(max-width: 480px)' : {width: "80%", marginLeft: "10%"}}}/><br></br>
    <TextField id="standard-basic" 
        label="Email" 
        variant="standard" 
        onChange={(e) => setEmail(e.target.value)}
        sx={{width: '50%', marginLeft:'25%', marginTop:'25px', '@media(max-width: 480px)' : {width: "80%", marginLeft: "10%"}}}/><br></br>
    <TextField id="standard-basic" 
        label="Telefonní číslo" 
        variant="standard" 
        onChange={(e) => setTelNumber(e.target.value)}
        sx={{width: '50%', marginLeft:'25%', marginTop:'25px', '@media(max-width: 480px)' : {width: "80%", marginLeft: "10%"}}}/><br></br>

   <InputLabel
   sx={{width: '50%', marginLeft:'25%', marginTop:'50px'}}
   >Fakulta</InputLabel>
  <Select
    labelId="demo-simple-select-label"
    id="demo-simple-select"
    sx={{width: '50%', marginLeft:'25%', marginTop:'10px', marginBottom: '50px', '@media(max-width: 480px)' : {width: "80%", marginLeft: "10%"}}}
    onChange={(e) => setSection(e.target.value)}
  >
     <MenuItem value={['PrF','Přírodovědecká fakulta']}>Přírodovědecká fakulta</MenuItem>
    <MenuItem value={['LF1', '1. Lékařská fakulta']}>1. Lékařská fakulta</MenuItem>
    <MenuItem value={['LF2', '2. Lékařská fakulta']}>2. Lékařská fakulta</MenuItem>
    <MenuItem value={['LF3', '3. Lékařská fakulta']}>3. Lékařská fakulta</MenuItem>
    <MenuItem value={['LFPLZ', 'Lékařská fakulta v Plzni']}>Lékařská fakulta v Plzni</MenuItem>
    <MenuItem value={['LFHRDC', 'Lékařská fakulta v Hradci Králové']}>Lékařská fakulta v Hradci Králové</MenuItem>
    <MenuItem value={['FaF', 'Farmaceutická fakulta']}>Farmaceutická fakulta</MenuItem>
    <MenuItem value={['FSV', 'Fakulta Sociálních věd']}>Fakulta sociálních věd</MenuItem>
    <MenuItem value={['FHS', 'Fakulta humanitních studií']}>Fakulta humanitních studií</MenuItem>
    <MenuItem value={['FF', 'Filozofická fakulta']}>Filozofická fakulta</MenuItem>
    <MenuItem value={['MFF', 'Matematicko-fyzikální fakulta']}>Matematicko-fyzikální fakulta</MenuItem>
    <MenuItem value={['KTF', 'Katolická teologická fakulta']}>Katolická teologická fakulta</MenuItem>
    <MenuItem value={['ETF', 'Evangelická teologická fakulta']}>Evangelická teologická fakulta</MenuItem>
    <MenuItem value={['HTF', 'Husitská teologická fakulta']}>Husitská teologická fakulta</MenuItem>
    <MenuItem value={['PF', 'Právnická fakulta']}>Právnická fakulta</MenuItem>
    <MenuItem value={['PEF','Pedagogická fakulta']}>Pedagogická fakulta</MenuItem>
    <MenuItem value={['FTVS', 'Fakulta tělesné výchovy a sportu']}>Fakulta tělesné výchovy a sportu</MenuItem>
  </Select>
  <Button 
  onClick={handleSubmit}
  color='error' sx={{marginBottom:'50px'}}>Zaregistrovat se</Button>
   </FormGroup> </Paper>
    </div>);
}
export default Register;