import Navbar from './Navbar';
import { Fade } from 'react-slideshow-image';
import 'react-slideshow-image/dist/styles.css'
import photo1 from '../Media/1.jpg';
import photo2 from '../Media/2.jpg';
import photo3 from '../Media/3.jpg';
import photo4 from '../Media/147144.png';
import photo5 from '../Media/2.jpg';
import textik from '../Media/text.json';
import WelcomeStyles from '../Styles/WelcomeStyles.css'
import {Card, Stack, Paper} from '@mui/material';


const Welcome = () => {
  document.body.style = 'background: #393e46;';
    const fadeImages  = [
        {
          url: photo1,
          caption: 'Slide 1'
        },
        {
          url: photo2, 
          caption: 'Slide 2'
        },
        {
          url: photo3,
          caption: 'Slide 3'
        },
        {
          url: photo5,
          caption: 'Slide 4'
        },
      ];


return(<div>

<Navbar/>  
<Card sx={{zIndex: '-1', maxHeight: '600px', position:'relative', top: 0}}>

        <Fade sx={{zIndex: '-1'}}>
        {fadeImages.map((fadeImage, index) => (
          <div className="each-fade" key={index}>
            <div className="image-container">
              <img className="imager" src={fadeImage.url} />
            </div>
          </div>
        ))} </Fade>
        </Card>
     
    <Card sx={{width: '65%', marginLeft: '10%', marginRight: '15%', position: 'absolute',
     zIndex: '1', left: '100px', top:'350px', padding:'2.5%', borderRadius: '16px', backgroundColor: '#B0B0B0'
      }}>
        <Stack direction="row" spacing={2}>
          <Paper sx={{width: '20%', marginLeft: '20%', height:'200px', backgroundColor:'#393e46'}}><h3 style={{marginLeft: "auto", marignRight: "auto", textAlign: 'center', color:'#d32f2f'}}>Kvalifikovaných pedagogů</h3>
          <h1 style={{marginLeft: "auto", marignRight: "auto", textAlign: 'center', color:'red'}}>2000+</h1></Paper>
          <Paper sx={{width: '20%', height:'200px', backgroundColor:'#393e46'}}>  <h3 style={{marginLeft: "auto", marignRight: "auto", textAlign: 'center', color:'#d32f2f'}}>Zkvalitněných přednášek</h3>
          <h1 style={{marginLeft: "auto", marignRight: "auto", textAlign: 'center', color:'green'}}>500+</h1></Paper>
          <Paper sx={{width: '20%', height:'200px', backgroundColor:'#393e46'}}>  <h3 style={{marginLeft: "auto", marignRight: "auto", textAlign: 'center', color:'#d32f2f'}}>Zapojených fakult v programu</h3>
          <h1 style={{marginLeft: "auto", marignRight: "auto", textAlign: 'center', color:'red'}}>19</h1></Paper>
        </Stack>
        <h1 style={{textAlign: 'left', color:'#d32f2f'}}>Pedagogium</h1><hr></hr>
        
        <div style={{width:'fit-content', float: 'left', textAlign: 'justify'}}>
        <Card sx={{width: '20%', float:'left', marginRight: '15px',  backgroundColor: '#393e46', paddingBottom: '25px'}}>
          <img src={photo4} style={{borderRadius: '150px', width:'80%', marginLeft:'10%', marginTop:'10%'}}></img>
          <h3 style={{marginLeft:'20%', color: '#d32f2f'}} >David Hůrný</h3><br></br>
          </Card>
          {textik.text}</div> <br></br><br></br>
          
</Card>
</div>
)}
export default Welcome;