import Navbar from './Navbar'
import EventsStyles from '../Styles/EventsStyles.css'
import React, {useEffect, useState} from 'react';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import {Button, Stack, Grid, Pagination} from '@mui/material';
import Typography from '@mui/material/Typography';
import useFetch3 from "../Requester/useFetch3";
import Post from './Post'



const Events = () => {
  document.body.style = 'background: #E0E0E0;';
    const {data3, setData3} = useFetch3();
    const [page, setPage] = useState(1);
    console.log(data3.results)
    useEffect(() => {
        let m = "https://pedagogium.onrender.com/posts/api/section/Events";
              setData3({ ...data3, slug: m });    
    setTimeout(()=>{
        }, 100)
      
      }, [])

      const handleChange = (event, valueR) => {
        setPage(valueR);
      };
return(<div>
<Navbar/>
<div id="MainContent">
<div id="Header"><br></br><br></br><br></br>
</div>
{!data3.results ? "Loading..." : 
              data3.results.slice(0).reverse().slice((page-1)*10,(page-1)*10+10).map(({ _id, caption, contentShort, content, section, url, date, imageURL}) =>
          <Post caption={caption} contentShort={contentShort} content={content} imageURL={imageURL} url={url} key={_id} _id={_id}></Post>
            )}
<div style={{display: 'block', width: 'fit-content', margin: 'auto'}}>    <Pagination count={Math.ceil((data3.results.length)/10)} page={page} onChange={handleChange} sx={{paddingBottom: '30px'}}/></div>
</div>
</div>);

      
      
}


export default Events;