import React, {useState, useEffect} from 'react';
import {CardContent, Card, Paper, Typography, Stack} from '@mui/material';
import useImage from "../Requester/useImage";


let Post = ({caption, name, imageURL, _id, className,...rest }) => {
    const { loading, error, image } = useImage('Excellent-teachers/'+imageURL);
    console.log('Excellent-teachers/'+imageURL);
    if (error) return <Typography>{"error"}</Typography>
    const detail = (x, y) => {
        if(x.includes("http")){
            let link = x;
            window.open(link, "_blank")
        }else{
            let link = 'https://pedagogium.onrender.com/detail/id/'+y;
            window.open(link, "_blank")
        }
    }

   return(<div>
<Paper sx={{height:'fit-content', padding: '30px', marginTop: '20px'}}>
  <Stack direction="row">
    <div style={{width: '30%'}}>
    <img  className={`Image${
                       className
                           ? className.padStart(className.length + 1)
                           : ''
                   }`}
                   src={image}
                   alt={'něco se pokazilo'}
                   {...rest} 
                   style={{width: '60%', marginLeft: '80px', borderRadius: '50%'}}></img>
    </div>
    <div style={{width: '70%'}}>
    <h1>{name}</h1>
    <p style={{width: '85%', textAlign: 'justify'}} >{caption}</p>
    </div>
  </Stack>
</Paper></div>
      );
}

export default Post;