import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import {Card, Color} from '@mui/material';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import {Button, ButtonGroup} from '@mui/material';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import Welcome from './Welcome'
import { ThemeProvider, createTheme } from '@mui/material/styles';
import { Link } from "react-router-dom";
import logo from '../Media/logo_violet.png';

export default function Navbar() {
  const theme = createTheme({
    palette: {
      primary: {
        main: "#f2f2f2"
      }
    }
  });
  return (
    <Card sx={{width: '75%', marginLeft:'15%', borderRadius: '16px', marginTop:'15px', marginBottom: "15px", backgroundColor:'#f2f2f2', position: 'absolute', zIndex: 5}} >
      <AppBar position="static" color="error" >
        <Toolbar sx={{marginLeft: 'auto', marginRight: 'auto', textAlign: 'center'}}>
        <a href="/about"><img src={logo} style={{width: '50px', marginLeft: '5%', marginRight:'2.5%'}}  href="/about" ></img></a>
        <ButtonGroup   variant="text" aria-label="text button group">
          <Button href="/about" variant="h6">
            Úvod
          </Button>
          <Button href="/events" variant="h6" >
            Akce
          </Button>
          <Button  href="/excellent-teachers" variant="h6">
            Excelentní učitelé
          </Button>
          <Button  href="/courses" variant="h6">
            Kurzy pedagogických dovedností
          </Button>
          <Button  href="/inspiration" variant="h6" >
            Návody a inspirace
          </Button>
          <Button  href="/science" variant="h6">
            Věda
          </Button></ButtonGroup>
</Toolbar>
      </AppBar>
    </Card>
    
  );
}



