import React, { useState, useEffect } from 'react';
import {Box, Tab, Tabs} from '@mui/material';

const CoursesNavbar = (props) => {



return(
<div style={{marginTop: '160px', position: 'absolute'}}>
<Box sx={{marginTop: '50px', marginLeft: '10px'}}>
      <Tabs
        orientation="vertical"
        aria-label="Vertical tabs example"
        sx={{ borderRight: 1, borderColor: 'divider' }}>
        <Tab label="Obsah kurzu"  href="/courses/" />
        <Tab label="Přednášející" href="/courses/lecturers"/>
        <Tab label="Reference"  href="/courses/references" />
        <Tab label="Kritérie absolvování"  href="/courses/criteria" />
        <Tab label="Přihlášení"  href="/courses/register" />
      </Tabs>
    </Box></div>);

}
export default CoursesNavbar;